import React from "react";
import { useSelector } from 'react-redux';
import "./overCheck.css";



function OverCheck() {
  const orderDetailCustom = useSelector((state) =>  state.orderDetail )
  const name = useSelector((state) =>  state.shoppingOrderDetail )
  const itemsArr = useSelector((state) =>  state.shoppingCartList )
  
  

  return (
    <>
        <div className="overDeyail">
          <h4 style={{ textDecoration: 'underline'}}>  {orderDetailCustom.order_serial_number} </h4>
          <div>(*Email)</div>
          <div> </div>
          {
            itemsArr.map((item) => (
              <>
                <div key={item.id}>{item.name} * {item.buy_num} $ {item.price}</div>
              </>
            ))
          }
          <div>  {orderDetailCustom.discount_count === 0 ? '' : '*1 -$ 100'}</div>
          <div>  $ {orderDetailCustom.pay_total}</div>
          <div>  { 
            orderDetailCustom.pay_way === 1 ?  'ATM' : 
            orderDetailCustom.pay_way === 2 ? '' :
            orderDetailCustom.pay_way === 3 ? 'Line' :
            'google pay' 
          }</div>
          <div>  
            {
              orderDetailCustom.deliver_way === 1 ? '' :
              orderDetailCustom.deliver_way === 2 ? '' : ''
            }
          </div>
          <div>  {name.name}</div>
          <div>  {`${orderDetailCustom.postal}${orderDetailCustom.city}${orderDetailCustom.address}`}</div>
        </div>
      
    </>
  );
}

export default OverCheck;
